@import './variables';


.transition {
  background-color: $accent-color;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  position: relative;
  perspective: 1800px;
}

.page-enter {

  &-active {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    animation: transitionEnter 0.5s cubic-bezier(0.37, 0, 0.65, 1) 0.25s both;
  }
}

.page-exit {
  
  &-active {
    animation: transitionLeave 0.5s cubic-bezier(0.37, 0, 0.65, 1) 0.25s both;
  }
}

.page-transition-inner {
  position: relative;
}

.page-enter, .page-exit {
  &-active {
    .page-transition-inner {
      height: 100vh;
      overflow: hidden;
      animation: transitionZoom 1s cubic-bezier(0.45, 0, 0.55, 1) both;
    }
  }
}

@keyframes transitionZoom {
  0% {
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
  }

  30% {
    transform: scale(0.6);
    -moz-transform: scale(0.6);
    -webkit-transform: scale(0.6);
  }

  70% {
    transform: scale(0.6);
    -moz-transform: scale(0.6);
    -webkit-transform: scale(0.6);
  }

  100% {
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
  }
}

@-webkit-keyframes transitionZoom {
  0% {
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
  }

  30% {
    transform: scale(0.6);
    -moz-transform: scale(0.6);
    -webkit-transform: scale(0.6);
  }

  70% {
    transform: scale(0.6);
    -moz-transform: scale(0.6);
    -webkit-transform: scale(0.6);
  }

  100% {
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
  }
}

@keyframes transitionLeave {
  from {
    transform: translateX(0);
  }

  to {
    transform: translateX(-100%);
  }
}

@keyframes transitionEnter {
  from {
    transform: translateX(150%);
  } 

  to {
    transform: translateX(0);
  }
}